import $ from 'jquery';

// 31996644065395 --- Free Lights Out Sample Pack
// 31810257223795 --- Free Metabolove with New Subscriptions
// 32113025613939 --- Free Sparkle Fiber with New Subscriptions
// 32148421542003 --- Free Love Wellness Guide to Sex & Your Vagina
// 17986205352051 --- Free Vitamin Organizer with New Subscriptions
// 39543426220147 --- Special Offer Do It All Wipes

// 13422120599667 --- Sleeping Beaty (trigger product)
// 39531486314611 --- Special Offer Sleep Mask (gift product)

// 39692994871411 --- New You '22 Kit (trigger product)
// 39703396679795 --- FREE Call Me Collagen 15 Day Supply (gift product)

/**
 * Adds free gift(s) to the cart under certain conditions
 * 
 * TODO: Update to refresh the cart content with AsyncView instead of reloading the entire page 
 *  like in StaticCart
 */

export default class FreeGift {
  constructor() {
    this.atLeastOneSubscriptionProduct = false;
    this.atLeastOneFreeProduct = false;
    this.atLeastOneGiftKit = false;
  }

  init(cart) {
    // Bail if no cart
    if(!cart) {
      return; 
    }

    this._processCartItems(cart.items);

    // If there is at least one subscription product on Cart hide additional payment buttons
    if(this.atLeastOneSubscriptionProduct) this._hideAdditionalCheckoutButtons();

    const enableGWP = true;

    if(enableGWP) {
      if(cart.items.length > 0) {
        /**
          GWP RULE #1: Add a gift on cart whenever at least 1 subscription product exists on Cart
        */
        const isSubscriptionGiftAdded = this._checkIfProductExistsOnCart(cart.items, 39883761188979);
        if(this.atLeastOneSubscriptionProduct) {
          if(!isSubscriptionGiftAdded) {
            this._addGift(39883761188979);
          } else {
            //removeGift(39883761188979);
          }
        } else {
          if(isSubscriptionGiftAdded) {
            this._removeGift(39883761188979);
          }
        }

        /**
          GWP RULE #2: Add a gift on cart whenever on the Cart is at least 1 Kit product from the list specified on "checkIfGiftKitExistsOnCart"
        */
        /** const isGiftAdded = checkIfProductExistsOnCart(cartItems, 32148421542003);
        if(this.atLeastOneGiftKit) {
          if(!isGiftAdded) {
            addKitGift();
          }
        } else {
          if(this.isGiftAdded) {
            removeGiftProducts();
          }
        } */

        /**
          GWP Rule #3: Gift product X when on Cart exists product Y
        */
        const isNewYouKitOnCart = this._checkIfProductExistsOnCart(cart.items, 39692994871411);
        const isFreeCMCOnCart = this._checkIfProductExistsOnCart(cart.items, 39703396679795);
        if(isNewYouKitOnCart) {
          if(!isFreeCMCOnCart) {
            this._addGift(39703396679795);
          } else {
            // removeGiftProducts();
          }
        } else {
          if(isFreeCMCOnCart) {
            this._removeGiftProducts();
          }
        }
      }

      if(cart.items.length === 1) {
        if (this.atLeastOneFreeProduct) {
          this._removeGiftProducts();
        }
      }
    }
  }

  _processCartItems(cartItems) {
    cartItems.forEach(item => {
      const itemVariantId = parseInt(item.variant_id);

      // Check if there is at least one sub product on the cart or not
      if(item.selling_plan_allocation !== undefined) { // changed on 15/11/2021
        this.atLeastOneSubscriptionProduct = true;
      }

      const isFreeProduct = this._checkIfProductIsFree(itemVariantId);
      if(isFreeProduct === true) {
        this.atLeastOneFreeProduct = true;
      }

      const isGiftKit = this._checkIfGiftKitExistsOnCart(itemVariantId);
      if(isGiftKit === true) {
        this.atLeastOneGiftKit = true;
      }
    });
  }

  _checkIfProductExistsOnCart(cartItems, variantId) {
    let productExists = false;

    cartItems.forEach(item => {
      if(item.variant_id === variantId) {
        productExists = true;
      }
    });

    return productExists;
  }

  _checkIfProductIsFree(variantId) {
    if(variantId === 31996644065395) return true; // Free Lights Out Sample Pack
    if(variantId === 31810257223795) return true; // Free Metabolove with New Subscriptions
    if(variantId === 32113025613939) return true; // Free Sparkle Fiber with New Subscriptions
    if(variantId === 32148421542003) return true; // Free Love Wellness Guide to Sex & Your Vagina
    if(variantId === 32211554336883) return true; // Free Vitamin Organizer with New Subscriptions
    if(variantId === 39531486314611) return true; // Special Offer Sleep Mask
    return false;
  }

  _checkIfGiftKitExistsOnCart(variantId) {
    if(variantId === 32146414796915) return true;
    if(variantId === 31257841500275) return true;
    if(variantId === 32146415943795) return true;
    if(variantId === 31468410765427) return true;
    if(variantId === 32146428788851) return true;
    if(variantId === 17992647704691) return true;
    if(variantId === 32146417942643) return true;
    if(variantId === 13894408765555) return true;
    if(variantId === 32152415600755) return true;
    return false;
  }
  
  _updateCartcount(cart) {
    const countEvent = new CustomEvent('cartcount:update', { detail: cart });
    window.dispatchEvent(countEvent);
  }

  _reloadCartPage(delay) {
    setTimeout(function() {
      window.location.href = '/cart';
    }, delay);
  }

  _hideAdditionalCheckoutButtons() {
    const additionalCheckoutButtons = document.querySelectorAll('.additional-checkout-buttons');

    additionalCheckoutButtons.forEach(function (button) {
      button.classList.add('hide-additional-checkout-buttons');
    });
  }

  _removeGiftProducts() {
    $.ajax({
      type: 'POST',
      url: '/cart/update.js',
      dataType: 'json',
      data: {
        updates: {
          39531486314611: 0,
          39543426220147: 0,
          17986205352051: 0,
          39703396679795: 0
        },
      },
      success: $.proxy(function (cart) {
        this._updateCartcount(cart);
        if (Shopify.cart) {
          this._reloadCartPage(300);
        }
      }, this),
      error: function (error) {
      },
    });
  }
  
  _removeGift(variantId) {
    $.ajax({
      type: 'POST',
      url: '/cart/update.js',
      dataType: 'json',
      data: {
        updates: {
          [variantId]: 0
        },
      },
      success: $.proxy(function(cart) {
        this._updateCartcount(cart);
        if (Shopify.cart) {
          this._reloadCartPage(300);
        }
      }, this),
      error: function (error) {
      },
    });
  }

  _addGift(variantId) {
    if(!variantId) return;
    $.ajax({
      type: 'POST',
      url: '/cart/update.js',
      dataType: 'json',
      data: {
        updates: {
          [variantId]: 1
        },
      },
      success: $.proxy(function (cart) {
        this._updateCartcount(cart);
        if (Shopify.cart) {
          this._reloadCartPage(300);
        }
      }, this),
      error: function (error) {
      },
    });
  }

}