import $ from 'jquery';
import Forms from '../Forms';
import GetSSOTribeToken from '../components/GetSSOTribeToken';
import { transition } from '@pixelunion/animations';

export default class Account {
  constructor() {
    this.$accountContents = $('[data-template-account]');
    this.$loginContent = $('[data-template-account-login]');
    this.$addressesContent = $('[data-template-account-addresses]');

    this.tabContents = document.querySelectorAll('[data-tab-content]');
    this.tabTriggers = document.querySelectorAll('[data-tab-trigger]');

    this.$addressButtonFocus = null;
    this.checkFillAnimation = null;
    this.checkAnimation = null;

    this._loginToggle = this._loginToggle.bind(this);
    this._setupTabs = this._setupTabs.bind(this);
    this._onTabClick = this._onTabClick.bind(this);

    if (this.$loginContent.length) {
      this._initLoginPage();
    }

    if (this.$addressesContent.length) {
      this.defaultAddressCheckbox =
        this.$addressesContent[0].querySelector('.checkmark');
      this.defaultAddressCheck =
        this.$addressesContent[0].querySelector('.checkmark__check');
      this.checkFillAnimation = transition({ el: this.defaultAddressCheckbox });
      this.checkAnimation = transition({ el: this.defaultAddressCheck });
      this._initAddressPage();
    }

    if (this.$accountContents.length) {
      this._init();
    }
  }

  _init() {
    new Forms(this.$accountContents);
    this._redirectToReferrer();
    this._setupTabs();
    new GetSSOTribeToken();
  }

  _initLoginPage() {
    this.$loginToggle = this.$loginContent.find('[data-login-toggle]');
    this.$login = this.$loginContent.find('[data-account-login-main]');
    this.$recovery = this.$loginContent.find('[data-account-login-recovery]');
    this.$recoveryHasMessage = this.$recovery.find(
      '[data-recovery-has-message]',
    );

    this.$loginToggle.on('click', this._loginToggle);

    if (this.$recoveryHasMessage.length) {
      this._loginToggle();
    }
  }

  _loginToggle(event = null) {
    if (event) {
      event.preventDefault();
    }

    this.$login.toggleClass('visible');
    this.$recovery.toggleClass('visible');
  }

  _redirectToReferrer() {
    const documentReferrer = window.location.href;
    const referrer = new URL(documentReferrer).searchParams.get('referrer');

    // TODO: Make dynamic so it works on staging as well
    if (referrer === 'https://lovewellness.com/pages/rewards') {
      window.location.assign(referrer);
    }
  }

  _initAddressPage() {
    const $customerAddresses = this.$addressesContent.find('[data-address-id]');

    this.$addressesContent.on('click', '[data-edit-address]', event => {
      const $target = $(event.currentTarget);
      const itemId = $target.attr('data-edit-address');

      $customerAddresses.removeClass('visible');
      this.$addressButtonFocus = $target;

      $(`[data-address-id="${itemId}"]`)
        .addClass('visible')
        .find('.form-field-input')
        .eq(0)
        .focus();
    });

    this.$addressesContent.on('click', '[data-edit-address-cancel]', () => {
      $customerAddresses.removeClass('visible');
      $('[data-address-id="new"]').addClass('visible');

      // Return focus to last used button
      this.$addressButtonFocus.focus();
      this.$addressButtonFocus = null;
    });

    this.$addressesContent.on('click', '[data-delete-address]', event => {
      const itemId = $(event.target).attr('data-delete-address');
      Shopify.CustomerAddress.destroy(itemId, '');
    });

    $customerAddresses.each((i, el) => {
      const id = $(el).attr('data-address-id');

      const countryEl = `customer_addr_${id}_country`;
      const provinceEl = `customer_addr_${id}_province`;
      const options = { hideElement: `address_province_container_${id}` };

      // Initiate provinces for address forms
      new Shopify.CountryProvinceSelector(countryEl, provinceEl, options);
    });

    this.$addressesContent.on('click', '#address_default_address_new', () => {
      let state = 'checked';
      if (this.defaultAddressCheckbox.dataset.animationState === 'checked') {
        state = 'unchecked';
      }

      this.checkFillAnimation.animateTo(state);
      this.checkAnimation.animateTo(state);
    });
  }

  _setupTabs() {
    this.tabTriggers.forEach(trigger =>
      trigger.addEventListener('click', this._onTabClick),
    );
  }

  _onTabClick(event) {
    event.preventDefault();

    const tabName = event.target.getAttribute('data-tab-trigger');

    this.tabTriggers.forEach(trigger => {
      if (trigger.getAttribute('data-tab-trigger') === tabName) {
        trigger.parentElement.classList.add('account-nav__item--active');
      } else {
        trigger.parentElement.classList.remove('account-nav__item--active');
      }
    });

    this.tabContents.forEach(content => {
      if (content.getAttribute('data-tab-content') === tabName) {
        content.style.display = 'block';
      } else {
        content.style.display = 'none';
      }
    });
  }
}
