export default class GetTribeToken {
  constructor() {
    window.addEventListener('load', this._retrieveToken.bind(this));
  }

  init() {
    this._retrieveToken();
  }

  /* Check to see if current customer is a member of the community.
  If so, get the token and add it to the "Community" link in the navigation.
*/
  _retrieveToken() {
    const accountObject = document.querySelector(
      '[data-tribe-current-user]',
    ).innerHTML;
    const tribeObject = document.querySelector('[data-tribe-fetch]').innerHTML;

    const data = JSON.parse(accountObject);
    const tribeFetchedData = JSON.parse(tribeObject);

    if (data === null || tribeFetchedData === null) {
      return;
    }

    fetch(`${tribeFetchedData.url}`, {
      method: 'POST',
      body: JSON.stringify(data),
    })
      .then(response => response.json())
      .then(data => {
        if (data.token) {
          const COMMUNITY_URL = 'https://club.lovewellness.com';
          var tribeSSOLink = `${COMMUNITY_URL}/auth/sso?ssoToken=${data.token}`;
          $(
            ".site-header .navmenu-item a[href$='club.lovewellness.com/']",
          ).attr('href', tribeSSOLink);
        }

        if ($('.tribe-login-link').length) {
          $('.tribe-login-link').show();
          $('.tribe-login-link').attr('href', tribeSSOLink);
        }
      })
      .catch(error => console.log(error));
  }
}
