import $ from 'jquery';
import ProductGridItem from '../components/ProductGridItem';
import SearchForm from '../components/search/SearchForm';

export default class StaticSearch {
  constructor(section) {
    this.section = section;
    this.$el = $(section.el);
    this.$searchField = this.$el.find('[data-live-search]');

    // Product items
    this.productItems = [];
    this._initProductItems();
    this._handleBoostRender();

    this.searchForm = new SearchForm(this.$searchField[0]);
  }

  _handleBoostRender() {
    //Listen for Boost re-rendering the page
    window.addEventListener('boostItemsRenders', () => {
      this._initProductItems(this.view);
    });
  }

  _initProductItems(view = 'grid-view') {
    const $productItems = this.$el.find('[data-product-item]');

    $productItems.each((i, productItem) => {
      this.productItems.push(
        new ProductGridItem({
          el: productItem,
          id: this.section.id,
          lazy: true,
          // grid_list: view,
        }),
      );
    });
  }

  onSectionUnload() {
    this.searchForm.unload();

    this.productItems.forEach(productItem => {
      productItem.unload();
    });
  }
}
